<template>
  <div id="app">
   <v-header :seller="seller"></v-header>
   <div class="app-content">
     <router-view/>
   </div>
   <div class="tab">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评价</router-link> 
      </div>
      <div class="tab-item">
         <router-link to="/seller">商家</router-link>
      </div>
   </div>
  </div>
</template>

<script>
  import header from './components/header/header'
  import {urlParse} from './common/js/util'
  const ERR_OK = 0;
export default {
  name: 'app',
  components:{
      'v-header':header
  },
  data(){
    return{
      seller:{
          id:(()=>{
            let queryParam = urlParse();
            return queryParam.id;
          })()

      }
    }
  },
  created(){
    console.log('组件被创建完成')
    this.$http.get('http://192.168.0.139:3000/api/seller').then((res)=>{
       res = res.body;
       if(res.errno === ERR_OK){
          this.seller = Object.assign({},this.seller,res.data)
       }
    })
  }
}
</script>

<style rel="stylesheet/scss" scoped="" lang="scss">
  @import "./common/stylus/_mixin" ;
   a{
     text-decoration:none;
   }
   .app-content{
    padding: 5px;
   }
   .tab{
      display: flex;
      width:100%;
      height:40px;
      line-height:40px;
      bottom: 0;
      position: absolute; 
      @include border-1px(rgba(7, 17, 27, 0.1));
      .tab-item{
        flex:1;
        text-align:center; 
        border-left: 1px solid rgba(7, 17, 27, 0.1);
        & > a{
           display:block;
          font-size:14px;
          color:rgb(77,85,93) ;
          &.active{
            color:rgb(240,20,20);
          }
        }
      }
        
         
   }
      
      


</style>



