<template>
	<div class="goods">
		<div class="menu-wrapper" ref="menuWrapper">
			<ul>
				<li v-for="item in goods" class="menu-item">
					<span class="text" >
						<span v-show="item.type >0" class="icon" :class="classMap[item.type]"></span>
						{{item.name}}
					</span>
				</li>
			</ul>
		</div>
		<div class="foods-wrapper" ref="foodWrapper">
			<ul>
				<li v-for="item in goods" class="food-list food-list-hook">
					<h1 class="title">{{item.name}}</h1>
					 <ul>
						 <li v-for="(food,index) in item.foods" class="food-item border-1px">
							 <div class="icon">
								 <img class="img" :src="food.icon" width="57" height="57">
							 </div>
							 <div class="content">
								 <h2 class="name">{{food.name}} </h2>
								 <p class="desc">{{food.description}}</p>
								 <div class="extra">
									 <span class="count">
										 月售{{food.sellCount}}份
									 </span>
								 </div>
								 <div class="price">
									 <span class="now">
										 ￥{{food.price}}
									 </span>
									 <span v-show="food.oldPrice" class="old">
										 ￥{{food.oldPrice}}
									 </span>
								 </div>
								 <div class="cartcontrol-wrapper">
									 	<cartcontrol :food="food"></cartcontrol>
								 </div>
							 </div>
						 </li>
					 </ul>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	import cartcontrol from '../cartcontrol/cartcontrol';
	import Bus from '../../common/js/EventBus';
	import BScroll from 'better-scroll';
	const ERR_OK = 0;
	const requsetUrl = 'http://192.168.0.139:3000';
	export default{
		props:{
			seller:{
				type:Object
			}
		},
		data(){
			return {
				goods:[],
				scrollY:0,
			}
		},
		created(){
			this.classMap = ['descrease','discount','special','invoice','guarantee']
			this.$http.get(requsetUrl+'/api/goods').then((response)=>{
				response = response.body;
				if(response.errno == ERR_OK){
					this.goods = response.data;
					//console.log(this.goods);
					this.$nextTick(()=>{
						this._initScroll();
					})
				}	
			})
		},
		methods:{
			_initScroll(){
				this.menuScroll = new BScroll(this.$refs.menuWrapper,{
					click:true
				})
				this.foodScroll = new BScroll(this.$refs.foodWrapper,{
					click:true,
					probeType:3
				})
				this.foodScroll.on('scroll',(pos)=>{
					this.scrollY = Math.abs(Math.round(pos.y));
				})
			}
		},
		components:{
			cartcontrol
		}
	}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
@import "../../common/scss/_mixins";
.goods{
	display: flex;
	overflow: hidden;
	position: absolute;
	top: 138px;
	bottom: 46px;
	width: 100%; 
	.menu-wrapper{
		flex: 0 0 80px;
		width: 80px;
		background: #f3f5f7;
		.menu-item{
			display: table;
			width: 56px;
			height: 54px;
			line-height: 14px;
			padding-left: 12px;
			.icon{
				display: inline-block;
				vertical-align: top;
				width: 12px;
				height: 12px;
				background-size: 12px 12px;
				background-repeat: no-repeat;
				&.descrease{
					@include bg-image('decrease_1');
				}
				&.discount{
					@include bg-image('discount_1');
				}
				&.guarantee{
					@include bg-image('guarantee_1');
				}
				&.invoice{
					@include bg-image('invoice_1');
				}
				&.special{
					@include bg-image('special_1');
				}
			}
			.text{
				font-size: 12px;
				display: table-cell;
				width: 56px;
				vertical-align: middle;
				text-align: center;
				@include border-1px(rgba(7, 17, 27, 0.1));
			}
		}
	}
	.foods-wrapper{
		flex: 1;
		.title{
			padding-left: 14px;
			height: 26px;
			line-height: 26px;
			border-left: 2px solid #d9dde1;
			font-size: 12px;
			color:rgb(147,153,159);
			background:#f3f5f7;
		}
		.food-item{
			display:flex;
			margin:18px;
			padding-bottom: 18px;
			@include border-1px(rgba(7,17,27,.1));
			&:last-child{
				@include border-none();
				margin-bottom: 0;
			}
			.icon{
				flex: 0 0 57px;
				margin-right:10px;
				img{
					border-radius: 5px;
				}
			}
			.content{
				flex:1;
				.name{
					margin:2px 0 8px;
					line-height: 14px;
					font-size: 14px;
					color:rgb(7,17,27);
				}
				.desc,
				.extra{
					font-size: 10px;
					color:rgb(147,153,159);
				}
				.desc{
					line-height: 12px;
					margin-bottom: 8px;
				}
				.extra{
					line-height: 10px;
					.count{
						margin-right:12px;
					}
				}
				.price{
					font-weight: 700;
					line-height: 24px;
					.now{
						margin-right: 8px;
						font-size: 14px;
						color:rgb(240,20,20);
					}
					.old{
						text-decoration: line-through;
						font-size: 10px;
						color: rgb(147,153,159);
					}
				}
				.cartcontrol-wrapper{
					position: absolute;
					right:0;
					bottom:12px;
				}
			}
		}
	}
}
</style>



