<template>
	<div class="ratings">
		评价
		<button class="btn-send" @click="save">发送</button>
    	<vue-editor id="editor" v-model="content"></vue-editor>
	</div>
</template>
<script>
	import { VueEditor } from 'vue2-editor'
	export default{
		name:'ratings',
		data(){
			return {
				content:""
			}
		},
		components: {
      		VueEditor
		},
		methods:{
			save:function(){
				this.$http.post('http://localhost:21986/api/auth',{"content":this.content}).then((response)=>{
					 console.log(response.body)
				})
			}
		}
	}
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
.ratings{
	padding: 5px;
	vertical-align: middle;
	.btn-send{
	   width: 60px;
	   height: 25px;
	   line-height: 26px;
	   vertical-align: top;
	   border: 1px solid #ed13f2;
	   border-radius: 50px;
	   background: black;
	   color: #fff;
	   font-size:10px;
	}
}

</style>


